<template>
    <div>
        <h1>{{id ? '编辑':'新增'}}分类</h1>
        <el-form @submit.native.prevent="handleSubmit">
            <el-form-item label="标题">
                <el-input v-model="model.title" />
            </el-form-item>
            <el-form-item label="所属类别">
                <el-select v-model="model.CatalogueId" placeholder="请选择">
                            <el-option
                              v-for="item in lists"
                              :key="item.id"
                              :label="item.title"
                              :value="item.id">
                            </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <template><span>正文</span></template>
                <vue-editor v-model="model.content"></vue-editor>
            </el-form-item>
            <el-form-item>
                <el-row>
                    <el-button type="primary" native-type="submit">提交</el-button>
                    <el-button>重置</el-button>
                </el-row>
            </el-form-item>
        </el-form>
    </div>
</template>
<style>

</style>
<script>
    import {
        VueEditor
    } from 'vue2-editor'

    export default {
        props:{
            id:{},
            },
        components: {
            VueEditor,
        },
        data() {
            return {
                model: {},
                lists:{},
            }
        },
        methods: {
            async init() {
                const res = await this.$http.get(`/articles/${this.id}`);
                // console.log(res.data)
                this.model = res.data.articles;
            },
            async getCatalogues(){
                const res = await this.$http.get(`/catalogues`);
                this.lists=res.data.catalogues;
                // console.log(res.data)
            },
            async handleSubmit(){
                let res;
                if(this.id){
                    res =await this.$http.put(`/articles/${this.id}`,this.model);
                    this.$router.push({name:'article-list'});
                    this.$message({
                        type:'success',
                        message:'修改成功',
                    })
                }else{
                    res =await this.$http.post(`/articles`,this.model);
                    this.$router.push({name:'article-list'});
                    this.$message=({
                        type:'success',
                        message:'创建成功',
                    })
                }
            }
        },
        created() {
            this.id && this.init();
            this.getCatalogues();
        }
    }
</script>